a {
  color: $link_colour;
  cursor: pointer;
  text-decoration: none;
  @include transition(all,-5);

  &:hover, &:active {
    @include link-underline(); } }
p, p li, blockquote {
  > a {
    color: $link_colour;
    text-decoration: none;
    &:hover {
      color: $link_colour;
      text-decoration: underline; } } }
