
@mixin splash_area_title_common($desktop_width: 33%,$splash_area_mobile_padding: rv($mobile_edge_padding_factor) * 2) {
  position: relative;
  width: 100%;
  justify-content: center;
  z-index: 1;
  padding: $splash_area_mobile_padding;
  border-left: $borderWidth solid $white;
  border-right: $borderWidth solid $white;
  border-bottom: $borderWidth solid $white;

  @include bp($stack) {
    position: absolute;
    top: 0;
    height: 100%;
    width: $desktop_width;
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: flex-start;
    border-left: $borderWidth solid $white;
    border-right: $borderWidth solid $white;
    border-bottom: 0; } }


@mixin splash_area_title_right($desktop_width: 33%, $splash_area_mobile_padding: rv($mobile_edge_padding_factor) * 2) {
  @include splash_area_title_common($desktop_width,$splash_area_mobile_padding);

  @include bp($stack) {
    right: 0; }

  @include bp($desktop) {
    padding: $title_area_outside_padding $title_area_outside_padding $title_area_bottom_padding $title_area_inside_padding; } }


@mixin splash_area_title_left($desktop_width: 33%,$splash_area_mobile_padding: rv($mobile_edge_padding_factor) * 2) {
  @include splash_area_title_common($desktop_width,$splash_area_mobile_padding);

  @include bp($stack) {
    left: 0; }

  @include bp($desktop) {
    padding: $title_area_outside_padding $title_area_inside_padding $title_area_bottom_padding $title_area_outside_padding; } }


@mixin splash_area_carousel_common() {
  overflow: hidden;
  flex: auto;
  width: 100%;
  margin-bottom: 0;

  @include bp($stack) {
    width: 67%;
    position: absolute;
    top: 0;
    bottom: 0; }

  .resource {
    @include bp($stack) {
      height: 100%;
      width: 100%;
      flex: 1;
      display: flex;
      figure {
        overflow: hidden;

        a {
          display: block;
          height: 100%; } }

      img {
        display: block;
        background-position: center bottom;
        object-fit: cover;
        height: 100%; } }
    &.slide-fit-cover {
      img {
        object-fit: cover; } }
    &.slide-fit-contain {
      img {
        object-fit: contain; } }

    &.slide-position-top-center {
      img {
        object-position: top center; } }
    &.slide-position-center-center {
      img {
        object-position: center center; } }
    &.slide-position-bottom-center {
      img {
        object-position: bottom center; } }

    &.slide-position-top-left {
      img {
        object-position: top left; } }
    &.slide-position-center-left {
      img {
        object-position: center left; } }
    &.slide-position-bottom-left {
      img {
        object-position: bottom left; } }

    &.slide-position-top-right {
      img {
        object-position: top right; } }
    &.slide-position-center-right {
      img {
        object-position: center right; } }
    &.slide-position-bottom-right {
      img {
        object-position: bottom right; } } } }

@mixin splash_area_carousel_right() {
  @include splash_area_carousel_common();

  @include bp($stack) {
    right: 0; } }

@mixin splash_area_carousel_left() {
  @include splash_area_carousel_common();

  @include bp($stack) {
    left: 0; } }


